@import url('https://fonts.googleapis.com/css2?family=Alice&family=Cambo&family=Cormorant:ital,wght@1,300;1,400;1,500;1,600&family=Crete+Round:ital@1&family=Dancing+Script:wght@600&family=Libre+Baskerville:wght@700&family=Open+Sans&family=Outfit:wght@300&family=PT+Serif&family=Playfair+Display:wght@500&family=Roboto+Flex:opsz,wght@8..144,100;8..144,200;8..144,400;8..144,500&family=Volkhov:ital,wght@1,700&family=Vollkorn:ital@1&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar{
    position: absolute;
    display: flex;
    font-family: 'Libre Baskerville', serif;
    justify-content: space-around;
    align-items: center;
    background-color: blueviolet;
    width: 100%;
    height: 75px;
    position: sticky;
}

.logo{
    color: white;
    font-size: 30px;
    font-weight: bold;
}

.nav-links{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
    font-family: 'Cormorant', serif;
}

.home,.about,.login,.register{
    color: white;
    font-weight: bolder;
    text-decoration: none;
    padding: 10px 20px;
    font-size: 25px;
}

.mob-menu-icon{
    display: none;
}

@media screen and (max-width:780px) {
    .logo{
        display: flex;
        position: absolute;
        text-align: center;
       }

    .nav-links{
        display: none;
    }

    .nav-links-mob{
        position: absolute;
        width: 100%;
        height: 280px;
        top: 75px;
        display: block;
        background-color: aqua;
        left: 0;
        transition: all 0.5s ease-out;
        color: black;
        }

        .home,.about,.login,.register{
            color: black;
            text-align: center;
            padding: 30px;
            width: 100%;
            transition: all 0.5s ease;
        }

        .mob-menu-icon{
            display: block;
            position: absolute;
            right: 20px;
            background-color: white;
            outline: none;
            border: none;
            width: 25px;
            font-size: 20px;
            height: 35px;
        }    
}