body{
  background-image: url(images/image1.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.home-head{
  margin-top: 20%;
  text-align: center;
  color: red;
  font-weight: bold;
  font-size: 50px;
}

.home-para{
  font-size: 30px;
  gap: 20px;
  padding: 10px 20px;
  color: white;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;

}

.btn{
  text-align: center;
  background-color: white;
  color: black;
  margin-left: 50%;
  height: 40px;
  width: 14%;
  border-radius: 15px;
  outline: none;
  border: none;
  border: 2px solid yellow;
  padding: 10px;
 }

 #login{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color:burlywood;
}

.form{
  width: 490px;
  background-color: rgb(247, 76, 247);
  border-radius: 15px;
  padding: 30px 40px;
}

.form h1{
  font-size: 35px;
  text-align: center;
}

.form .input-box{
  width: 100%;
  height: 50px;
  margin: 30px;
}

.input-box input{
  width: 90%;
  height: 90%;
  background: transparent;
  border: none;
  outline: none;
  border: 2px solid red;
  border-radius: 30px;
  padding:10px 20px;
  font-size: 20px;
}

.input-box input::placeholder{
  color: black;
}

.form .remember-forget{
  display: flex;
  justify-content: space-between;
  margin: -15px 0 15px;
  padding: 10px;
}

.remember-forget label input{
  color: black;
  margin-right: 3px;
}

.remember-forget a{
  color: black;
  text-decoration: none;
}

.remember-forget a:hover{
  text-decoration: underline;
}

.form .button{
  width: 100%;
  height: 45px;
  background-color: bisque;
  border: none;
  outline: none;
  border-radius: 40px;
  cursor: pointer;
  font-weight: bold;
}

.form .register-link{
  font-size: 15px;
  text-align: center;
  margin-top: 20px;
}

.register-link a{
  color: black;
  text-decoration: none;
  font-weight: bold;
}

#register{
  background-color: palevioletred;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.register-form{
  width: 490px;
  background-color:rgb(245, 119, 74);
  border-radius: 15px;
  padding: 30px 40px;
}

.register-form h1{
  text-align: center;
  font-size: 35px;
}

.register-form label{
  text-align: center;
  font-size: 20px;
 }

.register-form .input-box{
  width: 100%;
  height: 40px;
  padding: 10px 10px;
}

.register-btn{
  width: 100%;
  height: 50px;
  margin-top: 20px;
 background-color: greenyellow;
  border: none;
  outline: none;
  border-radius: 40px;
  cursor: pointer;
  font-weight: bold;
  padding: 20px;
}

input::placeholder{
  color: black;
}

#about{
  background-color: beige;
}

#about h1{
  font-size: 50px;
  text-align: center;
  color: yellow;
}


#about p{
  text-align: center;
  padding: 40px;
  font-size: 30px;
  color: white;
}

.about-image{
  width: 100%;
  height: 90vh;
  background-image: url('images/image3.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}





